     // PARA QUIEN ES? EN INGLES
      who:{
         title:"Who is it for?",
         info:"Men and women who tend to:",
         infowho:"  1. Live under constant pressure and stress.<br><br>2. Be emotional and reactive.<br><br>3. Get angry when things don't go their way.<br><br>4. Change their moods easily.<br><br>5. Have little patience and tolerance for others.<br><br>6. Be obsessive and overbearing.<br><br>7. React harshly to stimuli that irritate them.<br><br>8. Be arrogant when questioned or criticized.<br><br>9. Have difficulty accepting mistakes.<br><br>10. Blame others to justify their reactions. ",
         tambienes:"It is also designed for moms and dads who have strong-willed daughters and sons. Or who are neurodivergent.",
       },
       //COMO FUNCIONA?  EN INGLES
       how:{
         title:"How does it work?",
         info:"Angerpulse is based on short audio exercises that integrate:",
         respiracion:"Guided breathing with pulses.",
         musica:"Music designed with brain waves and specific sound frequencies.",
         impacto:"The positive impact of guided conscious breathing and music with specific combinations of waves/tones is scientifically proven to help us reduce stress and anxiety.<br><br>It helps us to relax, focus, clear our minds, and improve our mood.<br><br>Angerpulse combines breathing and music in a particular, practical, and simple way, that will help you to better regulate your negative emotions and contain anger impulses.",
          guia:" Quick guide to do the audio exercises: ",
          boton:"Press the center button on the Home screen and the audio exercises will automatically play. One after the other in a designed sequence.<br><br>You can listen to them at home, in your car or at work.<br><br>With headphones, speakers or directly on your cell phone.",
          segundos:"Within seconds of starting the audio exercise you will hear a percussion sound. ",
          udu:" It's called UDU and it will be your pulse, your breathing guide throughout the audio exercise. ",
          inhala:"When you hear the first UDU, inhale slowly and deeply through your nose.<br><br>Do not interrupt your inhalation until you hear the next UDU.<br><br>Then calmly exhale, gently and slowly, through your mouth.<br><br>Do not interrupt your exhalation until you hear the next UDU.<br><br>Continue alternating inhalations and exhalations each time you hear UDU.<br><br>Do so in a controlled, smooth, and slow manner. ",
          ritmo:" Your breathing rhythm will gradually slow down, and your inhalations and exhalations will become longer and longer.<br><br>If you feel difficulty at any point, simply breathe when you need to and follow the pulse again when you feel ready.<br><br>There are dozens of different audio exercises. You can save the ones you like in your audio library.",
        
        },
