        momento1:"1st Moment ",
        momentos:"Before everyday or planned activities that you know could generate pre-anger emotions.<br><br>Particularly stress, anxiety, anguish, fear, irritability, or frustration.",
        antes:"<li class='sb-16-gray py-2 text-start'>Before getting in the car to face the morning traffic.</li><li class='sb-16-gray py-2 text-start'>Before pressuring your children to get to school on time.</li><li class='sb-16-gray py-2 text-start'>Before a scheduled meeting with a difficult client or your boss.</li><li class='sb-16-gray py-2 text-start'>Before a prearranged talk with your partner to make a difficult decision.</li><li class='sb-16-gray py-2 text-start'>Before a social commitment with people you don't like very much.</li>",
        dinamica:"The focus here is on prevention.<br><br>On the organized regulation of pre-anger emotions so they don't accumulate.<br><br>Do an audio exercise before each of these activities that you know in advance.",
        momento2:" 2nd Moment ",
