        transicion:"When you are in transition between situations, activities, places or environments.",
        ej:" For example:",
        ejemplos:"<li class='sb-16-gray py-2 text-start'>Upon waking up, before starting your activities.</li><li class='sb-16-gray py-2 text-start'>Between one work meeting and another.</li><li class='sb-16-gray py-2 text-start'>When commuting from one place to another.</li><li class='sb-16-gray py-2 text-start'>When you're about to start working on something important and need to focus.</li><li class='sb-16-gray py-2 text-start'>When you get home from work.</li><li class='sb-16-gray py-2 text-start'> When you're getting ready to end the day and rest.</li>",
        audio:"In any transition from one situation to another. Do an audio exercise to be aware of what you should do now.",
